<template>
	<view class="content">
		<!-- 城市搜索 -->
		<view class="city-search-wrap" v-if="isReach">
			<view class="search">
				<view class="l-search">
					<text class="iconfont icon-search"></text>
					<input class="input-search" type="text" :value="inputValue" placeholder="输入城市名或拼音" placeholder-style="color:#8E8F97"
					 :focus="searchFocus" @input="searchChange" />
					<text class="clear-input iconfont icon-icon-test" v-if="isClearBtn" @click="inputValue = ''"></text>
				</view>
				<button class="r-cancel" open-type="navigateBack" delta="1" @click="isReach = false">
					取消
				</button>
			</view>
			<!-- 搜索列表  -->
			<view class="reach-content" v-show="inputValue">
				<block v-show="searchData.length">
					<view class="li borderBottom" v-for="item in searchData" :key="item.code" @click="$emit('select', item)">{{ item.name }}</view>
				</block>
				<view class="has-no-data" v-show="hasNoData">没有找到匹配数据~</view>
			</view>
		</view>
		<view class="t-index-address" v-show="!isReach">
			<view class="top-search">
				<button class="item" @click="isReach = true">
					<text class="iconfont icon-search"></text>
					<text>输入城市名或拼音</text>
				</button>
			</view>
			<scroll-view class="t-index-address__scroll-view" :scroll-into-view="scrollview" :scroll-y="true"
				:enable-flex="true">
				<view :id="group.initial" v-for="group in cityList" :key="group.initial">
					<view class="t-index-address__anchor">
						<text>{{ group.initial }}</text>
					</view>
					<view class="t-index-address__list">
						<view class="t-index-address__cell" v-for="(city, index) in group.list" :key="index"
							@click="$emit('select', city)">
							<text>{{ city.name }}</text>
						</view>
					</view>
				</view>
			</scroll-view>
			<view class="t-index-address__sidebar">
				<view class="t-index-address__index" v-for="group in cityList" :key="group.initial"
					@touchstart.stop.prevent="onTouchMove(group.initial)" @touchend.stop.prevent="onTouchStop"
					@touchcancel.stop.prevent="onTouchStop">
					<span>{{ group.initial }}</span>
				</view>
			</view>
			<view class="t-index-address__alert" v-if="touchmove">
				<text>{{ activeIndex }}</text>
			</view>
		</view>
	</view>
	
</template>

<script>
	import cityList from "./cities.json";

	export default {
		data() {
			return {
				scrollview: "A",
				cityList: [],
				activeIndex: "A",
				touchmove: false,
				
				isReach: false,
				inputValue: "",
				hasNoData: false,
				searchFocus: false,
				searchData: [], //搜索的数据
			};
		},
		watch: {
			activeIndex(value) {
				this.scrollview = value;
			},
			// 城市搜索输入框
			inputValue(newVal) {
				this.isClearBtn = newVal ? true : false;
		
				if (this.timer) {
					clearTimeout(this.timer);
				}
		
				if (!this.inputValue) {
					this.searchData = [];
					return;
				}
				this.timer = setTimeout(() => {
					const result = [];
					for (let i in this.cityList) {
						this.cityList[i].list.forEach(item => {
							if (
								item.name.includes(this.inputValue)
							) {
								result.push(item);
							}
						});
					}
					this.searchData = result;
					if (this.searchData.length === 0) {
						this.hasNoData = true;
						console.log('搜索不到');
					} else {
						this.hasNoData = false;
						console.log(result,'搜索的城市列表');
					}
				}, 500);
			},
			isReach(val) {
				this.searchFocus = val;
			}
		},
		methods: {
			//搜索
			searchChange(e) {
				let {
					value
				} = e.detail;
				this.inputValue = value;
			},
			
			
			initCityList() {
				this.cityList = cityList;
			},
			onTouchMove(index) {
				this.activeIndex = index;
				this.touchmove = true;
			},
			onTouchStop() {
				this.touchmove = false;
			},
		},
		mounted() {
			this.initCityList();
		},
	};
</script>

<style lang="scss" scoped>
	.city-search-wrap {
		width: 100%;
		padding: 0 30rpx;
		box-sizing: border-box;
	
		.search {
			width: 100%;
			height: 60rpx;
			display: flex;
			align-items: center;
			font-size: 24rpx;
			color: #222;
			padding: 0 30rpx;
			box-sizing: border-box;
			position: fixed;
			left: 0;
			top: 0;
			z-index: 1;
			background: #fff;
	
			.l-search {
				flex: 1;
				background: #f4f5f9;
				border-radius: 50rpx;
				position: relative;
	
				.icon-search {
					font-size: 26rpx;
					position: absolute;
					left: 30rpx;
					top: 50%;
					transform: translateY(-50%);
					color: #8e8f97;
					font-weight: 700;
				}
	
				.input-search {
					height: 60rpx;
					box-sizing: border-box;
					padding: 0 100rpx 0 100rpx;
					text-align: center;
				}
	
				.clear-input {
					font-size: 30rpx;
					position: absolute;
					right: 10rpx;
					top: 50%;
					transform: translateY(-50%);
					padding: 10rpx;
					color: #8e8f97;
				}
			}
	
			.r-cancel {
				margin-left: 28rpx;
				padding-right: 30rpx;
				padding: 0 16rpx;
				font-size: 30rpx;
				background: transparent;
			}
		}
	}
	.top-search {
		height: 60rpx;
		line-height: 60rpx;
		padding: 0 30rpx;
		box-sizing: border-box;
		margin-bottom: 20rpx;
	
		.item {
			background: #f4f5f9;
			border-radius: 50rpx;
			font-size: 24rpx;
			text-align: center;
			color: #8e8f97;
	
			text {
				padding-left: 20rpx;
				color: #c1c2cd;
			}
	
			.icon-search {
				font-size: 26rpx;
				color: #8e8f97;
				font-weight: 700;
			}
		}
	}
	
	.reach-content {
		padding-top: 60rpx;
	
		.li {
			font-size: 32rpx;
			color: #333;
			padding: 20rpx 0;
			position: relative;
		}
	}
	
	.has-no-data {
		font-size: 24rpx;
		text-align: center;
		color: #8e8f97;
		margin-top: 50rpx;
	}
	
	.t-index-address {
		height: 100%;

		&__scroll-view {
			width: 100%;
			height: 100%;
			max-height: 100vh;
		}

		&__anchor {
			padding: 15rpx 30rpx;
			width: 100%;
			font-size: 28rpx;
			font-weight: 500;
			color: #606266;
			background-color: rgb(245, 245, 245);
		}

		&__list {
			padding: 0 70rpx 0 30rpx;
		}

		&__cell {
			height: 100rpx;
			line-height: 100rpx;
			border-bottom: 1rpx solid #f2f2f2;

			&:last-child {
				border: none;
			}
		}

		&__sidebar {
			position: fixed;
			top: 50%;
			right: 0;
			transform: translateY(-50%);
			z-index: 99;
		}

		&__index {
			padding: 10rpx 20rpx;
			font-size: 22rpx;
			font-weight: 500;
			line-height: 1;
		}

		&__alert {
			position: fixed;
			top: 50%;
			right: 90rpx;
			z-index: 99;
			margin-top: -60rpx;
			width: 120rpx;
			height: 120rpx;
			font-size: 50rpx;
			color: #fff;
			border-radius: 24rpx;
			background-color: rgba(0, 0, 0, 0.5);
			display: flex;
			justify-content: center;
			align-items: center;
		}
	}
</style>
